<template>
  <v-col cols="12" class="text-center">
    <v-card style="background: transparent;" color="transparent" class="elevation-0 px-0 mx-0" @click="dialogSeleccionarCafeteria = true">
      <h1 class="grey--text mx-0 px-0">
        <span v-if="$cafeteriaSeleccionada && $cafeteriaSeleccionada.id">{{ $cafeteriaSeleccionada.nombre }}</span>
        <span v-else>Seleccione cafetería</span>
      </h1>
    </v-card>

    <dialogo v-model="dialogSeleccionarCafeteria" title="Seleccionar cafetería">
      <v-card v-for="(cafeteria, indexCafeteria) in cafeterias" :key="indexCafeteria" class="text-center" @click="seleccionarCafeteria(cafeteria)">

        <v-card-text>
          <h3 class="v-btn font-weight-bold">
            <v-img class="mr-2" width="30px" src="https://cdn.pixabay.com/photo/2019/08/27/04/18/store-icon-4433328_960_720.png"/>
            {{cafeteria.nombre}}
          </h3>
        </v-card-text>
      </v-card>
    </dialogo>
  </v-col>
</template>

<script>
import CafeteriasService from "@/services/CafeteriasService";

export default {
  name: "SelectCafeteria",
  data(){
    return{
      dialogSeleccionarCafeteria: false,
      cafeterias: []
    }
  },
  async created() {
    try {
      this.cafeterias = await CafeteriasService.getByEncargado(this.$usuario.id)
    } catch (e) {
      this.cafeterias = []
    }
  },
  methods: {
    seleccionarCafeteria (cafeteria) {
      this.$cafeteriaSeleccionada = cafeteria
      this.dialogSeleccionarCafeteria = false
    }
  }
}
</script>

